      MENU: []
    }
  },
  created () {
    // console.log(this.$router)
    // this.MENU = this.filterItem( this.iMenu, 0 )
    this.$store.commit('setMenu', this.filterItem( this.iMenu, 0 ))
    // console.log( this.filterItem( this.iMenu, 0 ) )
  },
  methods: {
    filterItem ( data, parent ) {

      const MENU = []
      let temp

      for (let i = 0; i < data.length; i++) {
        if ( data[i].ParentId == parent ) {
          const obj = data[i]
          temp = this.filterItem(data, data[i].Id)
          if (temp.length > 0) {
            obj.subs = temp
          }
          MENU.push(obj)
        }
      }

      return MENU
    }
  }
}
